import './App.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import Form from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'
import React from 'react'
import axios from 'axios'
import { clearSequence, getReverseComplementSequenceString } from './Helpers'

class Spacer extends React.Component {
  constructor(props) {
    super(props)
  }
  onSeqChange = (event) => {
    this.props.parentSeqCallback(this.props.spacer.id, event.target.value)
  }
  onOhposChange = (event) => {
    this.props.parentOhposCallback(this.props.spacer.id, event.target.value)
  }
  render() {
    let spacer = this.props.spacer
    const oh_pos = () => {
        if(spacer.id == 1)
        return <Form.Control class="spacer-oh_pos" onChange={this.onOhposChange} disabled type="number" />
        return <Form.Control class="spacer-oh_pos" onChange={this.onOhposChange} type="number" value={spacer.pos} min="1" max={spacer.seq.length} />
    }
    return (
      <div class="spacer-container alert alert-info border">
        <h4>Spacer #{spacer.id}</h4>
        <Form.Group controlId={"formSpacer-" + spacer.id}>
          <div class="row">
            <div class="col-8">
              <Form.Label htmlFor="spacer-seq">Sequence <span class={"badge bg-danger " + spacer.gwarning}>Warning: No init G</span></Form.Label>
              <Form.Control class="spacer-seq" value={spacer.seq} onChange={this.onSeqChange}/>
              <Form.Text className="text-muted">
                ~20 nt sequence
              </Form.Text>
            </div>
            <div class="col-4">
              <Form.Label htmlFor="spacer-oh_pos">OH position</Form.Label>
              {oh_pos()}
            </div>
          </div>
        </Form.Group>
      </div>
    )
  }
}

class Spacers extends React.Component {
  constructor(props) {
    super(props)
  }
  handleSeqCallback = (id, sequence) => {
    this.props.parentSeqCallback(id, sequence)
  }
  handleOhposCallback = (id, pos) => {
    this.props.parentOhposCallback(id, pos)
  }
  render() {
    const items = () => {
      let content = []
      let spacers = this.props.spacers
      for (let idx in spacers) {
        content.push(<Spacer spacer={spacers[idx]} parentSeqCallback = {this.handleSeqCallback} parentOhposCallback = {this.handleOhposCallback} />)
      }
      return content
    }
    return (
      <div id="spacers-container">
      <h2>Spacers</h2>
        {items()}
      </div>
    )
  }
}

class Ohs extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      clip_class: "bi-clipboard"
    }
  }
  onCopy(e, copy_txt){
    navigator.clipboard.writeText(copy_txt)
    this.setState({
      clip_class: "bi-clipboard-check"
    })
    setTimeout(() => {
      this.setState({
        clip_class: "bi-clipboard"
      })
    }, 1000)
  }
  render(){
    const items = () => {
      let content = ""
      for (let idx in this.props.ohs){
        if(content != ""){
          content = content + ", "
        }
        content = content + this.props.ohs[idx]
      }
      return content
    }
    return(
      <div id="results-container" class="alert alert-light border">
        <h2 class="results-title">OHs</h2>
        <div class="results">
          {items()} <i id="clipboard-ohs" class={"bi " + this.state.clip_class} onClick={(e) => this.onCopy(e, items())}></i>
        </div>
        <br/>
        <a href="https://ggtools.neb.com/viewset/run.cgi" class="fs-6 badge bg-secondary fw-normal text-decoration-none" target="_blank">Ligation Fidelity Viewer <i class="bi bi-box-arrow-up-right"></i></a>
      </div>
    )
  }
}

class Result extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      clip_class: "bi-clipboard"
    }
  }
  onCopy(e, copy_txt){
    navigator.clipboard.writeText(copy_txt)
    this.setState({
      clip_class: "bi-clipboard-check"
    })
    setTimeout(() => {
      this.setState({
        clip_class: "bi-clipboard"
      })
    }, 1000)
  }
  render(){
    const oligo = this.props.oligo
    const default_classes = 'badge fw-normal '
    let result_seq =
    <div class="result-seq">
      <span class={default_classes + "bg-warning"}>{oligo.aari}</span>
      <span class={default_classes + "bg-primary text-uppercase text-decoration-underline"}>{oligo.promoter_oh}</span>
      <span class={default_classes + "bg-info text-dark text-lowercase"}>{oligo.spacer}</span>
      <span class={default_classes + "bg-success"}>{oligo.fwd_primer_3}</span>
    </div>
    if (oligo.type == "fwd_inbet"){
      result_seq =
      <div class="result-seq">
        <span class={default_classes + "bg-warning"}>{oligo.aari}</span>
        <span class={default_classes + "bg-primary"}>{oligo.promoter_oh}</span>
        <span class={default_classes + "bg-info text-dark"}><span class="text-uppercase text-decoration-underline">{oligo.spacer_oh}</span><span class="text-lowercase">{oligo.spacer_cont}</span></span>
        <span class={default_classes + "bg-success"}>{oligo.fwd_primer_3}</span>
      </div>
    }
    if (oligo.type == "rev_inbet"){
      result_seq =
      <div class="result-seq">
        <span class={default_classes + "bg-warning"}>{oligo.aari}</span>
        <span class={default_classes + "bg-primary"}>{oligo.promoter_oh}</span>
        <span class={default_classes + "bg-info text-dark"}><span class="text-uppercase text-decoration-underline">{oligo.spacer_oh}</span><span class="text-lowercase">{oligo.spacer_cont}</span></span>
        <span class={default_classes + "bg-danger"}>{oligo.rev_primer_3}</span>
      </div>
    }
    if (oligo.type == "rev_last"){
      result_seq =
      <div class="result-seq">
        <span class={default_classes + "bg-warning"}>{oligo.aari}</span>
        <span class={default_classes + "bg-secondary text-uppercase text-decoration-underline"}>{oligo.terminator_oh}</span>
        <span class={default_classes + "bg-danger"}>{oligo.rev_primer_3}</span>
      </div>
    }
    return (
      <div class="result-container alert alert-light border">
        <div class="result-name fw-bold">
          {oligo.name} ({oligo.len} nt) <i id={"clipboard-" + oligo.name} class={"bi " + this.state.clip_class} onClick={(e) => this.onCopy(e, oligo.copy_txt)}></i>
        </div>
        {result_seq}
      </div>
    )
  }
}

class Results extends React.Component {
  constructor(props) {
    super(props)
  }
  render(){
    const items = () => {
      let content = []
      for (let idx in this.props.oligos) {
        content.push(<Result oligo={this.props.oligos[idx]} />)
      }
      return content
    }
    const ohs = () => {
      let content = []
      for (let idx in this.props.oligos) {
        const oligo_oh = this.props.oligos[idx].oh
        if(oligo_oh){
          content.push(oligo_oh)
        }
      }
      return content
    }
    return (
      <div id="results-container">
        <div id="results-container-primers" class="alert alert-light border">
          <h2 class="results-title">Primer list</h2>
          <div class="results">
            {items()}
          </div>
        </div>
        <Ohs ohs={ohs()} />
      </div>
    )
  }
}

class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      spacers: [],
      promoter_oh: 'ctcc',
      terminator_oh: 'tttt',
      fwd_primer_3: 'GTTTTAGAGCTAGAAATAGCAAGTTAAAATAAGGC',
      rev_primer_3: 'tggagcgggaaacgagactc',
      aari: 'aaCACCTGCaaca',
      oligos: []
    }
  }
  handleSeqCallback = (id, sequence) => {
    var new_spacers = this.state.spacers
    for (let idx in new_spacers) {
      if (new_spacers[idx].id == id){
        let clearedSequence = clearSequence(sequence)
        let gwarning = "collapse"
        if(id == 1 && clearedSequence.length > 0 && clearedSequence.charAt(0) != 'G' && clearedSequence.charAt(0) != 'g'){
          gwarning = "collapse show"
        }
        new_spacers[idx].seq = clearedSequence
        if(new_spacers[idx].pos > clearedSequence.length) new_spacers[idx].pos = clearedSequence.length
        new_spacers[idx].gwarning = gwarning
        break
      }
    }
    this.setState({
      spacers: new_spacers
    })
    this.designOligos()
  }
  handleOhposCallback = (id, pos) => {
    var new_spacers = this.state.spacers
    for (let idx in new_spacers) {
      if (new_spacers[idx].id == id){
        new_spacers[idx].pos = pos
        break
      }
    }
    this.setState({
      spacers: new_spacers
    })
    this.designOligos()
  }
  designOligos = () => {
    let oligos = []
    let spacers = this.state.spacers
    for (let idx in spacers){
      // Forward
      let newOligoName = 'GTR-' + spacers[idx].id + '-F'
      let newOligoF = {
        'name': newOligoName,
      }
      if (idx == 0){
        //  first
        newOligoF['aari'] = this.state.aari
        newOligoF['promoter_oh'] = this.state.promoter_oh
        newOligoF['spacer'] = spacers[idx].seq
        newOligoF['fwd_primer_3'] = this.state.fwd_primer_3

        newOligoF['oh'] = newOligoF['promoter_oh']
        newOligoF['len'] = newOligoF['aari'].length + newOligoF['promoter_oh'].length + newOligoF['spacer'].length + newOligoF['fwd_primer_3'].length
        newOligoF['copy_txt'] = newOligoName + "\t" + newOligoF['aari'] + newOligoF['promoter_oh'] + newOligoF['spacer'] + newOligoF['fwd_primer_3']
      } else {
        // any in between
        newOligoF['aari'] = this.state.aari
        newOligoF['spacer_oh'] = spacers[idx].seq.substring(parseInt(spacers[idx].pos) - 1 , parseInt(spacers[idx].pos) + 3)
        newOligoF['spacer_cont'] = spacers[idx].seq.substring(parseInt(spacers[idx].pos) + 3)
        newOligoF['fwd_primer_3'] = this.state.fwd_primer_3

        newOligoF['oh'] = newOligoF['spacer_oh']
        newOligoF['type'] = "fwd_inbet"
        newOligoF['len'] = newOligoF['aari'].length + newOligoF['spacer_oh'].length + newOligoF['spacer_cont'].length + newOligoF['fwd_primer_3'].length
        newOligoF['copy_txt'] = newOligoName + "\t" + newOligoF['aari'] + newOligoF['spacer_oh'] + newOligoF['spacer_cont'] + newOligoF['fwd_primer_3']
      }

      // Reverse
      newOligoName = 'GTR-' + spacers[idx].id + '-R'
      let newOligoR = {
        'name': newOligoName,
      }
      if (spacers.length == 1 || idx == spacers.length - 1) {
        // last or length = 1
        newOligoR['aari'] = this.state.aari
        newOligoR['terminator_oh'] = getReverseComplementSequenceString(this.state.terminator_oh)
        newOligoR['rev_primer_3'] = this.state.rev_primer_3


        newOligoR['oh'] = getReverseComplementSequenceString(newOligoR['terminator_oh'])
        newOligoR['type'] = "rev_last"
        newOligoR['len'] = newOligoR['aari'].length + newOligoR['terminator_oh'].length + newOligoR['rev_primer_3'].length
        newOligoR['copy_txt'] = newOligoName + "\t" + newOligoR['aari'] + newOligoR['terminator_oh'] + newOligoR['rev_primer_3']
      } else {
        // any in beetwen
        const next_idx = parseInt(idx) + 1

        newOligoR['aari'] = this.state.aari
        newOligoR['spacer_oh'] = getReverseComplementSequenceString(spacers[next_idx].seq.substring(parseInt(spacers[next_idx].pos) - 1 , parseInt(spacers[next_idx].pos) + 3))
        newOligoR['spacer_cont'] = getReverseComplementSequenceString(spacers[next_idx].seq.substring(0, parseInt(spacers[next_idx].pos - 1)))
        newOligoR['rev_primer_3'] = this.state.rev_primer_3
        newOligoR['type'] = "rev_inbet"
        newOligoR['len'] = newOligoR['aari'].length + newOligoR['spacer_oh'].length + newOligoR['spacer_cont'].length + newOligoR['rev_primer_3'].length
        newOligoR['copy_txt'] = newOligoName + "\t" + newOligoR['aari'] + newOligoR['spacer_oh'] + newOligoR['spacer_cont'] + newOligoR['rev_primer_3']
      }
      oligos.push(newOligoF)
      oligos.push(newOligoR)
    }
    this.setState({
      oligos: oligos
    })
  }
  onAariOhChange = (event) => {
    this.setState({
      aari: clearSequence(event.target.value)
    })
    this.designOligos()
  }
  onFWdPrimer3Change = (event) => {
    this.setState({
      fwd_primer_3: clearSequence(event.target.value)
    })
    this.designOligos()
  }
  onRevPrimer3Change = (event) => {
    this.setState({
      rev_primer_3: clearSequence(event.target.value)
    })
    this.designOligos()
  }
  onTerminatorOhChange = (event) => {
    this.setState({
      terminator_oh: clearSequence(event.target.value)
    })
    this.designOligos()
  }
  onPromoterOhChange = (event) => {
    this.setState({
      promoter_oh: clearSequence(event.target.value)
    })
    this.designOligos()
  }
  addSpacer = () => {
    let new_spacers = this.state.spacers
    new_spacers.push({
      id: this.state.spacers.length + 1,
      seq: '',
      gwarning: 'collapse'
    })
    this.setState({
      spacers: new_spacers
    })
  }
  componentDidMount(){
    this.designOligos()
  }
  render() {
    return (
      <div class="container">
        <h1>GTR Multiplexing Designer</h1>
        <div class="row">
          <div class="col-7">
            <div class="alert alert-light border">
              <Spacers spacers={this.state.spacers} parentSeqCallback = {this.handleSeqCallback} parentOhposCallback = {this.handleOhposCallback} />
                <Form.Group controlId="formAddButton">
                  <Button onClick={this.addSpacer}>+1</Button>
                </Form.Group>
            </div>
            <div class="alert alert-light border">
              <h2 id="inputs-title">Inputs</h2>
              <Form.Group controlId="formPromoterInput" class="alert alert-primary border">
                <Form.Label htmlFor="spacer-seq"><span class="badge bg-primary">Promoter OH</span></Form.Label>
                <Form.Control class="spacer-seq" value={this.state.promoter_oh} onChange={this.onPromoterOhChange} />
                <Form.Text className="text-muted">
                  4 base OH (Default: pBadTss1)
                </Form.Text>
              </Form.Group>
              <Form.Group controlId="formTerminatorInput" class="alert alert-secondary border">
                  <Form.Label htmlFor="spacer-seq"><span class="badge bg-secondary">Terminator OH</span></Form.Label>
                  <Form.Control class="spacer-seq" value={this.state.terminator_oh} onChange={this.onTerminatorOhChange} />
                  <Form.Text className="text-muted">
                    4 base OH (Default: tttt → common to L1-2-pBadTSS1-crRNA_entry and L1-2-pU6_26-crRNA_entry)
                  </Form.Text>
              </Form.Group>
              <Form.Group controlId="formFwdPrimerInput" class="alert alert-success border">
                  <Form.Label htmlFor="spacer-seq"><span class="badge bg-success">3' Fwd primer (sgRNA)</span></Form.Label>
                  <Form.Control class="spacer-seq" value={this.state.fwd_primer_3} onChange={this.onFWdPrimer3Change} />
                  <Form.Text className="text-muted">
                    Bases for Tm = 60 (5' → 3') (Default: From GTR_sgRNA_scaff_tRNA_Gly_coli)
                  </Form.Text>
              </Form.Group>
              <Form.Group controlId="formRevPrimerInput" class="alert alert-danger border">
                  <Form.Label htmlFor="spacer-seq"><span class="badge bg-danger">3' Rev primer (tRNA)</span></Form.Label>
                  <Form.Control class="spacer-seq" value={this.state.rev_primer_3} onChange={this.onRevPrimer3Change} />
                  <Form.Text className="text-muted">
                    Bases for Tm = 60 (5' → 3') (Default: From GTR_sgRNA_scaff_tRNA_Gly_coli)
                  </Form.Text>
              </Form.Group>
              <Form.Group controlId="formAariInput" class="alert alert-warning border">
                  <Form.Label htmlFor="spacer-seq"><span class="badge bg-warning">AarI</span></Form.Label>
                  <Form.Control class="spacer-seq" value={this.state.aari} onChange={this.onAariOhChange} />
                  <Form.Text className="text-muted">
                    Extra bases + AarI RE site + any 4 bases
                  </Form.Text>
              </Form.Group>
            </div>
          </div>
          <div class="col-5">
            <Results oligos={this.state.oligos} />
          </div>
        </div>
      </div>
    )
  }
}

export default App
